<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list ref="tgList" :listType="['List']" :IsShowAction="true" :showCheckBox="true" ModuleName="Tax"
            SubModuleCode="TaxJurisdiction" IdentityColumn="id" :HeaderText="$t('Tax Jurisdiction')" :ListData="ListingData"
            :HeaderData="Headers" :widgets="widgets" :HeaderButtons="Buttons" :TotalRecords="TotalRecords"
            :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchTaxJurisdictionList" :SortExp="SortExp"
            :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata"
            :SearchFields="leftSearchFields" :LegendArray="LegendArray" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:ActionButtonClick="actionButtonClick">
            <template v-slot:slotdata="{ data }">                
                <template v-if="data.column.COLUMN_NAME == 'name'">                      
                     <a class="task-dec" @click="AddTaxJurisdiction(data.row)">  {{ data.row.name }}</a>                                                
                </template>                
                 <template v-if="data.column.COLUMN_NAME == 'status_name'">                       
                    <a class="inactive-disabled">
                        <span class="text-center text-white d-block" :style="{backgroundColor: data.row.status_bg_color}"> {{ data.row.status_name }} </span>
                    </a>                                           
                </template>                
            </template>
        </tg-list>
        <AddTaxJurisdiction v-if="isAddTaxJurisdiction" @CloseTaxJurisdiction="CloseTaxJurisdiction" @ReloadTaxJurisdiction="ReloadTaxJurisdiction" :TaxJurisdictionData="TaxJurisdictionData"></AddTaxJurisdiction>
        <MapTaxJurisdiction v-if="IsMapTaxJurisdiction" @CloseMapJurisdiction="CloseMapJurisdiction" :TaxJurisdictionId ="MapTaxJurisdictionData"></MapTaxJurisdiction>
    </section>
</template>
<script>
import TaxCategorySchema from './TaxJurisdictionSchema';
import AddTaxJurisdiction from './AddTaxJurisdiction.vue';
import MapTaxJurisdiction from './MapTaxJurisdiction.vue';
import DataService from '../../services/DataService'
export default {
    components:{AddTaxJurisdiction,MapTaxJurisdiction},
    data() {
        return {
            TotalRecords: 0,
            isAddTaxJurisdiction:false,
            isLoading: false,
            CompanyId: null,
            TaxJurisdictionData:null,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            LegendArray: [],
            SortBy: '',
            SortExp: '',
            listActions: TaxCategorySchema.listActions,
            Headers: TaxCategorySchema.TableHeader,
            IsAssignedUser: false,
            leftSearchFields: TaxCategorySchema.leftsearchSchema,
            Buttons: TaxCategorySchema.buttons,
            ListingData: [],
            searchCondition: '',
            AssigenedUser: '',
            Headers: [
                {
                    "COLUMN_NAME": "name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Jurisdiction Name",
                    "DISPLAY_ORDER": 1,
                    settings : {
                            isInSlot: true
                        }
                },
                {
                    "COLUMN_NAME": "description",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Description",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "country_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Country",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "state_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "State",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "city",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "City",
                    "DISPLAY_ORDER": 3    
                },
                 {
                    "COLUMN_NAME": "code",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Code",
                    "DISPLAY_ORDER": 3    
                },
                {
                    "COLUMN_NAME": "zip_code",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Zip Code",
                    "DISPLAY_ORDER": 3    
                },
                {
                    "COLUMN_NAME": "status_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Status",
                    "DISPLAY_ORDER": 3,
                    settings : {
                            isInSlot: true
                        }                    
                }
            ],
            UserId:null,
            CompanyId:null,
            MapTaxJurisdictionData:null,
            IsMapTaxJurisdiction:false
        }
    },
    created:function(){
        this.UserId = this.GetUserInfo.ID;
        this.CompanyId = this.GetUserInfo.CompanyId;  
        this.FetchTaxJurisdictionList();
    },
    methods:{
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if (value.searchConditionJson != '') {
                    const fvalue = value.searchConditionJson.split('=')[1];
                    this.fav = fvalue;
                } else {
                    this.searchCondition = value.searchCondition;
                }
            }
            else {
                this.searchCondition = "";
            }
            this.FetchTaxJurisdictionList();
        },      
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {                    
                    case 'Edit':
                        action.isVisible = true;
                        break;  
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    case 'Tax Map':
                        action.isVisible = true;
                        break;                      
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchTaxJurisdictionList();
        },
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {              
                case 'AddTaxJurisdiction':
                    this.AddTaxJurisdiction(item);
                    break;
                case 'DeleteTaxJurisdiction':
                    this.commonDeleteData(item);
                    break; 
                case 'MapTaxJurisdiction':
                    this.MapTaxJurisdiction(item.id);
                    break;                    
            }
        },
        commonHeaderButtonClick: async function(event) {
            var vm=this;
            switch (event.callbackfunction) {
                case "AddTaxJurisdiction":
                    vm.AddTaxJurisdiction(null);
                    break;                
                case "DeleteTaxJurisdiction":
                    vm.commonDeleteData();
                    break;
            }
        },      
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchTaxJurisdictionList();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        }, 
        AddTaxJurisdiction(val){ 
            this.TaxJurisdictionData=val;
            this.isAddTaxJurisdiction=true;
        },      
        CloseTaxJurisdiction(){
            this.isAddTaxJurisdiction=false;
        },
        ReloadTaxJurisdiction(){
            this.FetchTaxJurisdictionList();
        },
        FetchTaxJurisdictionList(){            
            var vm = this;
            vm.isLoading=true;
            var url=`pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
            DataService.FetchTaxJurisdictionList(url).then((response)=>{             
                vm.isLoading=false;                
                if(response.data.result.DATA.length>0){
                    vm.ListingData=response.data.result.DATA;
                    vm.TotalRecords = response.data.result.DATA[0].TOTAL_RECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                }else{
                    vm.ListingData=[];
                    vm.TotalRecords = 0;
                }
            })
        },
        commonDeleteData: function (value) { 
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (value == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = value.id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("TaxJurisdiction")), function () {     
                    var url=`ids=${val}&Type=TAX_Jurisdiction`;
                    DataService.RemoveMapping(url).then(function (response) { 
                        vm.isLoading=false;
                        if(response.data.result.DATA.length>0){
                            if(response.data.result.DATA[0].VALUE=='DELETE_TAX_JURISDICTION'){
                                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('TaxJurisdiction')]), "Success", true, vm.$t('Alert'));
                                vm.FetchTaxJurisdictionList();
                            }else{
                                vm.ShowAlert(vm.$t('DeletedError', [vm.$t('TaxJurisdiction')]), "failure", true, vm.$t('Alert'));
                            }                     
                        }else{
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('TaxJurisdiction')]), "failure", true, vm.$t('Alert'));
                        }               
                    }) 
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        }, 
        MapTaxJurisdiction(val){
            this.MapTaxJurisdictionData=val;
            this.IsMapTaxJurisdiction=true;
        },
        CloseMapJurisdiction(){
             this.IsMapTaxJurisdiction=false;
        }
    }
}
</script>
<style>
.task-dec {color: #007bff !important; text-decoration: none; background-color: transparent;}
</style>