<template>
    <section>
        <!-- <loader :is-visible="isLoading"></loader> -->
        <div class="ticketsmallfix theme-primary partition p-actions-expand " style="overflow: visible;">
            <span class="p-name text-white">{{ $t("Tax Product Type Mapping") }}</span>
            <span class="p-actions float-right">
                <a title="Map" @click="AddProductType(null)" href="javascript:;" class="p-action-btn text-white">
                    <em class="fa fa-plus pr-2 "></em> <br><span>{{ $t('Map') }}</span></a>             
            </span>
        </div>
        <div class="listing mt-2">
            <div>

                <div class="table-responsive table-fix-header">
                    <table id="tablelistingdata_155625" class="table table-bordered mt-0 tablecolfix200"
                        style="width: 100%;">
                        <thead class="thead-bg">
                            <tr>                               
                                <th id="product_type_name" class="" style="text-transform: capitalize;">
                                    <span> {{$t('AssetTypeItem')}} </span>
                                </th>
                                <th id="name" class="" style="text-transform: capitalize;">
                                    <span> {{$t('TaxName')}} </span>
                                </th>
                                <th id="effective_from" class="" style="text-transform: capitalize;">
                                    <span> {{$t('EffectiveFrom')}} </span>
                                </th>
                                <th id="effective_to" class="" style="text-transform: capitalize;">
                                    <span> {{ $t('EffectiveTo') }} </span>
                                </th>
                                <th id="rate_type" class="" style="text-transform: capitalize;">
                                    <span> {{$t('RateType')}} </span>
                                </th>
                                <th id="is_slab_rate" class="" style="text-transform: capitalize;">
                                    <span> {{ $t('TaxRate') }} </span>
                                </th>
                                <th id="status_name" class="text-center" style="text-transform: capitalize;">
                                    <span> {{$t('Status')}} </span>
                                </th>
                                <th id="Action" class="text-center" style="text-transform: capitalize;">
                                    <span>{{ $t('Action') }}</span>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(items, type) in ListingData" :key="type">                           
                                <td>{{ items.group_name }}</td>
                                <td colspan="7">
                                    <table class="table table-bordered mt-0 tablecolfix200_inner" style="width: 100%;">

                                        <tbody>
                                            <tr v-for="(gitems, gtype) in items.GroupData" :key="gtype">
                                                <td>{{ gitems.name }}</td>
                                                <td>{{ $options.filters.formatDate(gitems.effective_from) }}</td>
                                                <td>{{ $options.filters.formatDate(gitems.effective_to) }}</td>
                                                <td>
                                                    <span v-if="gitems.rate_type == 'P'">
                                                        {{$t('Percentage')}}
                                                    </span>
                                                    <span v-else>{{ $t('Flat') }}</span>
                                                </td>
                                                <td>
                                                    <span v-if="gitems.is_slab_rate">
                                                        <a class="task-dec" @click="ShowSlabDeails(gitems.id)">{{$t('Slab')}}</a>
                                                    </span>
                                                    <span v-else>{{ gitems.rate }}%</span>
                                                </td>
                                                <td class="text-center">
                                                    <a class="inactive-disabled">
                                                        <span class="text-center text-white d-block"
                                                            :style="{ backgroundColor: gitems.status_bg_color }"> {{
                                                            gitems.status_name }} </span>
                                                    </a>
                                                </td>
                                                <td class="text-center single-action">
                                                    <a typetitle="Edit" typefor="Edit"
                                                        @click="AddProductType(gitems.id)" title="Edit"
                                                        class="text-success mr-4">
                                                        <em class="fa fa-pencil action-icon"></em>
                                                    </a>
                                                    <a typetitle="Delete" typefor="Delete"
                                                        @click="commonDeleteData(gitems.id)" title="Delete">
                                                        <em class="fa fa-trash-o action-icon text-danger "></em>
                                                    </a>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </td>
                            </tr>
                            <tr v-if="ListingData.length === 0">
                                <td class="text-center text-danger no-record" colspan="8">
                                    {{ $t('NoRecordfound') }}
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>               
                <Pager  v-bind:numberofrecords="TotalRecords" :CurrentPageNumber="PageNumber" prev-text="Prev" first-text="First" last-text="Last" next-text="Next"  v-on:SetCurrentPage="pagerMethod"></Pager>
            </div>
        </div>        
        <MapProductType v-if="IsProductType" @CloseMapProductType="CloseMapProductType"
            @GetProductTypeList="GetProductTypeList" :TaxCategoryData="TaxCategoryData"></MapProductType>
        <SlabDetalis v-if="IsSlabDetalis" @CloseSlabDeails="CloseSlabDeails" :TaxCategoryData="TaxCategoryData"
            :Type="Type">
        </SlabDetalis>
    </section>
</template>
<script>
import DataService from '../../services/DataService'
import MapProductType from './MapProductType.vue';
import SlabDetalis from './SlabDetalis.vue';
import Pager from '../../components/Common/Pager.vue';
export default {
    components: { MapProductType, SlabDetalis,Pager },
    data() {
        return {
            isLoading: false,
            IsProductType: false,
            Type: 'Tax_Product_Type',
            TaxCategoryData: null,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            TotalRecords: 0,
            widgets: [],
            LegendArray: [],
            checkedItems: [],
            selectAll: false,
            SortBy: '',
            SortExp: '',
            ListingData: [],
            searchCondition: '',
            IsSlabDetalis: false
        }
    },
    created: function () {
        var ths = this;
        ths.GetProductTypeList();
    },
    methods: {       
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.GetProductTypeList();
        },
        GetProductTypeList() {            
            var vm = this;
            vm.isLoading = true;
            var url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
            DataService.GetProductTypeList(url).then((response) => {
                vm.isLoading = false; 
                if(response.data.result.DATA.length>0){
                    if (Array.isArray(response?.data?.result?.DATA)) {
                        vm.TotalRecords = response.data.result.DATA[0].TOTAL_RECORDS;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;                    
                        const groupedByProductType = response.data.result.DATA.reduce((acc, item) => {   
                             if (typeof item.asset_type !== 'string') {
                                return acc; 
                            }             
                            const key = item.asset_type.trim()           
                            if (!acc[key]) {
                                acc[key] = [];
                            }
                            acc[key].push(item);
                            return acc;
                        }, {});
                        const formattedData = Object.keys(groupedByProductType).map(groupName => {
                            return {
                                group_name: groupName,
                                GroupData: groupedByProductType[groupName]
                            };
                        });
                        this.ListingData = formattedData;
                    }
                }else{
                    this.ListingData=[]
                }         
                
            })
        },
        commonDeleteData: function (value) {            
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (value == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = value;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Tax Category")), function () {
                    var url = `ids=${val}&Type=TAX_PRODUCT_TYPE`;
                    DataService.RemoveMapping(url).then(function (response) {
                        vm.isLoading = false;
                        if (response.data.result.DATA.length > 0) {
                            if (response.data.result.DATA[0].VALUE == 'DELETE_TAX_PRODUCT_TYPE') {
                                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('TaxProductType')]), "Success", true, vm.$t('Alert'));
                                vm.GetProductTypeList();
                            } else {
                                vm.ShowAlert(vm.$t('DeletedError', [vm.$t('TaxProductType')]), "failure", true, vm.$t('Alert'));
                            }
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('TaxProductType')]), "failure", true, vm.$t('Alert'));
                        }
                    })
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        CloseMapProductType() {
            this.IsProductType = false;
        },
        AddProductType(val) {
            this.TaxCategoryData = val;
            this.IsProductType = true;
        },
        ShowSlabDeails(val) {
            this.TaxCategoryData = val;
            this.IsSlabDetalis = true;
        },
        CloseSlabDeails() {
            this.IsSlabDetalis = false;
        }       
    }
}
</script>
<style>
.table-responsive .tablecolfix200 thead tr th:nth-child(1) {
    width: 50px !important;
    min-width: 50px;
    max-width: 50px;
}
.table-responsive .tablecolfix200 thead tr th {
    width: 150px;
    min-width: 150px;
    max-width: 150px;
}
.table-responsive .tablecolfix200_inner tbody tr td {
    width: 150px;
    min-width: 150px;
    max-width: 150px;
}
</style>
