<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>

        <!-- Layout when type !== 'Tax' -->
        <section v-if="LayoutType !== 'Tax'">
            <div class="col-md-12 p-0">
                <div class="tab-content">
                    <div class="dynamiccontent_base">
                        <dynamic-form lang="en" :buttons="false" :schema="FormSchema" ref="AddTaxCategoryForm"
                            :mandatorytext='MandatoryString' v-on:OnSubmit="OnSubmit">
                            <template v-slot:tgslot-SlabRate="{ data }">
                                <div class="container-fluid collapse show p-0" id="collapseOne1">
                                    <div class="listing">
                                        <div class="table-responsive">
                                            <table id="Addresslist"
                                                class="table table-bordered dt-responsive nowrap mt-0">
                                                <thead class="thead-bg">
                                                    <tr>
                                                        <th width="5%" class="text-center">
                                                            #
                                                        </th>
                                                        <th class="text-center">
                                                            {{ $t('AmountFrom') }}
                                                        </th>
                                                        <th class="text-center">
                                                            {{ $t('AmountTo') }}
                                                        </th>
                                                        <th class="text-center">
                                                            {{ $t('TaxRate') }}
                                                        </th>
                                                        <th class="text-center">
                                                            {{ $t('Actions') }}
                                                        </th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr v-if="RateData.length === 0">
                                                        <td class="text-center text-danger no-record" colspan="8">
                                                            {{ $t('NoRecordfound') }}
                                                        </td>
                                                    </tr>
                                                    <tr v-else v-for="(item, index) in RateData" :key="index">
                                                        <td class="text-center">{{ index + 1 }}</td>
                                                        <td class="text-center">
                                                            <input type="text" class="form-control"
                                                                v-model="item.AmountFrom"
                                                                :disabled="RateData.length === 1 || index === 0"
                                                                @blur="validateAmounts(index)" />
                                                        </td>
                                                        <td class="text-center">
                                                            <input type="text" class="form-control"
                                                                v-model="item.AmountTo"
                                                                :disabled="RateData.length === 1 || index === RateData.length - 1"
                                                                @blur="validateAmounts(index)"
                                                                @change="updateNextAmountFrom(index)" />
                                                        </td>
                                                        <td class="text-center">
                                                            <input type="text" class="form-control"
                                                                v-model="item.TaxRate" @blur="formatTaxRate(index)" />
                                                        </td>
                                                        <td class="text-center">
                                                            <a href="javascript:;" v-if="index == 0"
                                                                @click="addChild()"><em
                                                                    class="round-icon-small fa fa-plus bg-success float-none"></em></a>
                                                            <a href="javascript:;" @click="deleteItem(index)"
                                                                v-if="index > 0"><em
                                                                    class="round-icon-small fa fa-trash bg-danger float-none"></em></a>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>
                            </template>
                        </dynamic-form>
                    </div>
                    <div class="mainfooter_app ">
                        
                            <div class="col-lg-12 text-right mt-4">
                                <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                                    v-if="currentTabIndex > 1">
                                    <i class="fa fa-chevron-left pr-2"></i> {{ $t('Previous') }}
                                </button>

                                <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4"
                                    @click="Skip()" v-if="tabCount > currentTabIndex">
                                    {{ $t("Skip") }}
                                </button>

                                <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip"
                                    data-original-title="Save"
                                    class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                                    @click="OnSubmit('SaveAndNext')" style="margin-left: 5px"
                                    v-if="tabCount > currentTabIndex">
                                    <i class="fa fa-save pr-2"></i> {{ $t("SaveAndNext") }}
                                </button>

                            </div>
                        
                    </div>
                </div>
            </div>
        </section>

        <!-- Original modal layout when type === 'Tax' -->
        <div v-else class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('Tax') }} {{ $t('Category') }}</h4>
                        <button type="button" class="close" @click="CloseTaxCategory">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" ref="AddTaxCategoryForm"
                            :mandatorytext='MandatoryString' v-on:OnSubmit="OnSubmit">
                            <template v-slot:tgslot-SlabRate="{ data }">
                                <div class="container-fluid collapse show p-0" id="collapseOne1">
                                    <div class="listing">
                                        <div class="table-responsive">
                                            <table id="Addresslist"
                                                class="table table-bordered dt-responsive nowrap mt-0">
                                                <thead class="thead-bg">
                                                    <tr>
                                                        <th width="5%" class="text-center">
                                                            #
                                                        </th>
                                                        <th class="text-center">
                                                            {{ $t('AmountFrom') }}
                                                        </th>
                                                        <th class="text-center">
                                                            {{ $t('AmountTo') }}
                                                        </th>
                                                        <th class="text-center">
                                                            <span v-show="isTaxRate">{{ $t('TaxRates') }}</span>
                                                            <span v-show="!isTaxRate">{{ $t('TaxRates') }}(%)</span>
                                                            <!-- {{ $t('TaxRates') }} -->
                                                        </th>
                                                        <th class="text-center">
                                                            {{ $t('Actions') }}
                                                        </th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr v-if="RateData.length === 0">
                                                        <td class="text-center text-danger no-record" colspan="8">
                                                            {{ $t('NoRecordfound') }}
                                                        </td>
                                                    </tr>
                                                    <tr v-else v-for="(item, index) in RateData" :key="index">
                                                        <td class="text-center">{{ index + 1 }}</td>
                                                        <td class="text-center">
                                                            <!-- <input type="text" class="form-control"
                                                                v-model="item.AmountFrom"
                                                                :disabled="RateData.length === 1 || index === 0"
                                                                @blur="validateAmounts(index)" /> -->

                                                            <input type="text" class="form-control"
                                                                v-model="item.AmountFrom"
                                                                @blur="validateAmounts(index)" />
                                                        </td>
                                                        <td class="text-center">
                                                            <!-- <input type="text" class="form-control"
                                                                v-model="item.AmountTo"
                                                                :disabled="RateData.length === 1 || index === RateData.length - 1"
                                                                @blur="validateAmounts(index)"
                                                                @change="updateNextAmountFrom(index)" /> -->

                                                            <input type="text" class="form-control"
                                                                v-model="item.AmountTo"
                                                                @blur="validateAmounts(index)"
                                                                @change="updateNextAmountFrom(index)" />
                                                        </td>
                                                        <td class="text-center">
                                                            <input type="text" class="form-control"
                                                                v-model="item.TaxRate" @blur="formatTaxRate(index)" />
                                                        </td>
                                                        <td class="text-center">
                                                            <a href="javascript:;" v-if="index == 0"
                                                                @click="addChild()"><em
                                                                    class="round-icon-small fa fa-plus bg-success float-none"></em></a>
                                                            <a href="javascript:;" @click="deleteItem(index)"
                                                                v-if="index > 0"><em
                                                                    class="round-icon-small fa fa-trash bg-danger float-none"></em></a>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>
                            </template>
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../services/DataService'
export default {
    props: {
        TaxCategoryData: {
            type: Object,
            Required: true
        },
        ClientId: {
            type: Number,
            required: true,
        },
        LayoutType: {
            type: String,
            required: true
        },
        tabCount: 0,
        currentTabIndex: 0
    },
    data() {
        return {
            MandatoryString: 'Fields marked with an asterisk (*) are mandatory.',
            isLoading: false,
            TaxCategoryId: 0,
            Type: '',
            isTaxRate: true,
            RateData: [{
                TaxSlabId: 0,
                AmountFrom: '1.00',
                AmountTo: '\u221E',
                TaxRate: '00.00'
            }],
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Tax Category Name'),
                            name: "TaxCategoryName",
                            value: "",
                            disabled: false,
                            visibility: true,
                            placeholder: this.$t('Tax Category Name'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "1001",
                            visibility: true,
                            config: {
                                options: [{ name: 'Active', value: 1001 }, { name: 'InActive', value: 1002 }]
                            },
                            placeholder: this.$t('Select Status'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('Effective From '),
                            name: "EffectiveFrom ",
                            value: new Date(),
                            visibility: true,
                            placeholder: this.$t('Effective From'),
                            config: {
                                format: "M/D/YYYY",
                                minDate: new Date(),
                                onChange: this.ChangeEffectiveFrom
                            }
                        },
                        {
                            astype: "DateField",
                            label: this.$t('Effective To '),
                            name: "EffectiveTo",
                            value: '',
                            visibility: true,
                            placeholder: this.$t('Effective To'),
                            config: { format: "M/D/YYYY", minDate: '' }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Rate Type'),
                            name: "RateType",
                            disabled: false,
                            value: "",
                            visibility: true,
                            config: {
                                options: [{ name: 'Flat', value: 'F' }, { name: 'Percentage', value: 'P' }],
                                onChange: this.changeRateLabel
                            },
                            placeholder: this.$t('Select Status'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "toggleField",
                            label: this.$t("Tax Slab"),
                            name: "TaxSlab",
                            value: "",
                            disabled: false,
                            placeholder: "",
                            visibility: true,
                            config: {
                                onChange: this.ChangeSlabRate
                            },

                        },
                        {
                            astype: "NumericField",
                            label: this.$t('Rate(%)'),
                            name: "Rate",
                            value: "",
                            visibility: true,
                            placeholder: this.$t('Enter Rate'),
                            validationRules: {
                                required: true,
                                max: 100, // for actual numeric comparison
                                // regex: /^(100(?:\.0{1,2})?|(?:\d{1,2})(?:\.\d{1,2})?)$/
                            },
                            config: {
                                onKeyUp: this.validateRatePercentage
                            }
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            name: "SlabRate",
                            value: "",
                            visibility: false,
                            validationRules: { "required": false, max: 100 },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Description'),
                        name: "Description",
                        value: "",
                        config: {
                            rows: 3
                        },
                        placeholder: this.$t('Enter Tax Description'),
                        validationRules: {
                            "required": false
                        }
                    }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.CloseTaxCategory,
                },
            ]
        }
    },
    created: async function () {
        debugger
        var ths = this;
        ths.LayoutType;
        if (ths.TaxCategoryData != null && ths.ClientId == null) {
            ths.TaxCategoryId = this.TaxCategoryData;
            ths.Type = 'Tax_Category';
            ths.TaxCategoryDetails('Tax_Category');
        }
        if (ths.TaxCategoryData != null && ths.ClientId != null) {
            ths.TaxCategoryId = this.TaxCategoryData;
            ths.Type = 'Tax_Client';
            ths.TaxCategoryDetails('Tax_Client');
        }
    },
    methods: {
        validateRatePercentage: function(event) {
            var ths = this;
            var rateType= ths.FormSchema[0].Data[4].value;
            if(rateType == 'P'){
                const input = event.target;
                let value = parseFloat(input.value);
                if (isNaN(value)) {
                    return; // not a number, skip
                }
                if (value > 100) {
                    input.value = ''; // clamp to 100
                } else if (value < 0) {
                    input.value = '0'; // clamp to 0 if negative input
                }
            }
        },
        ChangeEffectiveFrom(val) {
            var EffectiveFrom = val;
            this.FormSchema[0].Data[3].config.minDate = EffectiveFrom;
        },
        ChangeSlabRate() {
            var ths = this;
            var slabevalue = ths.FormSchema[0].Data[5].value;
            if (slabevalue) {
                ths.FormSchema[0].Data[6].visibility = false;
                ths.FormSchema[1].Data[0].visibility = true;
            } else {
                ths.FormSchema[1].Data[0].visibility = false;
                ths.FormSchema[0].Data[6].visibility = true;
            }
        },
        addChild() {
            const lastItem = this.RateData[this.RateData.length - 1];
            const taxRate = parseFloat(lastItem.TaxRate);
            if (isNaN(taxRate) || taxRate <= 0) {
                this.ShowAlert(this.$t('CannotAddNew'), "warning", true, this.$t("Alert"));
                return false;
            }
            this.RateData.push({
                AmountFrom: '00.00',
                AmountTo: '\u221E',
                TaxRate: '00.00'
            });
        },
        formatTaxRate(index) {
            var ths=this;
            var rateType= ths.FormSchema[0].Data[4].value;
            if(rateType == 'P'){
                const input = event.target;
                let value = parseFloat(input.value);
                if (isNaN(value)) {
                    return; // not a number, skip
                }
                if (value > 100) {
                    input.value = ''; // clamp to 100
                } else if (value < 0) {
                    input.value = '0'; // clamp to 0 if negative input
                }
            }
            // const item = this.RateData[index];
            // let val = parseFloat(item.TaxRate);
            // if (isNaN(val) || val < 0) {
            //     this.$set(this.RateData[index], 'TaxRate', '00.00');
            // } else {
            //     this.$set(this.RateData[index], 'TaxRate', val.toFixed(2));
            // }
        },
        updateNextAmountFrom(index) {
            if (index < this.RateData.length - 1) {
                const currentTo = parseFloat(this.RateData[index].AmountTo);
                if (!isNaN(currentTo)) {
                    const nextFrom = (currentTo + 0.01).toFixed(2);
                    this.$set(this.RateData[index], 'AmountTo', currentTo.toFixed(2));
                    this.$set(this.RateData[index + 1], 'AmountFrom', nextFrom);
                }
            }
        },
        validateAmounts(index) {
            const item = this.RateData[index];
            const amountFrom = parseFloat(item.AmountFrom);
            const amountTo = parseFloat(item.AmountTo);
            if (isNaN(amountFrom) || isNaN(amountTo)) return;
            if (amountFrom > amountTo) {
                this.ShowAlert(this.$t('AmountFromCannot'), "warning", true, this.$t("Alert"));
                if (i < this.RateData.length - 1) {
                    const currentTo = parseFloat(this.RateData[i].AmountTo);
                    if (!isNaN(currentTo)) {
                        const nextFrom = (currentTo + 0.01).toFixed(2);
                        this.$set(this.RateData[i + 1], 'AmountFrom', nextFrom);
                    }
                }
            }
            if (amountTo < amountFrom) {
                this.ShowAlert(this.$t('AmountToCannot'), "warning", true, this.$t("Alert"));
                this.$set(this.RateData[index], 'AmountTo', '00.00');
            }
            if (index < this.RateData.length - 1) {
                const currentTo = parseFloat(this.RateData[index].AmountTo);
                if (!isNaN(currentTo)) {
                    const nextFrom = (currentTo + 0.01).toFixed(2);
                    this.$set(this.RateData[index], 'AmountTo', currentTo.toFixed(2));
                    this.$set(this.RateData[index + 1], 'AmountFrom', nextFrom);
                }
            }
        },
        deleteItem(itemIndex) {
            this.RateData.splice(itemIndex, 1);
        },
        CloseTaxCategory: function () {
            var vm = this;
            vm.$emit('CloseTaxCategory');
        },
        ReloadTaxCategory() {
            var vm = this;
            vm.$emit('ReloadTaxCategory');
        },
        TaxCategoryDetails(type) {
            var ths = this;
            ths.isLoading = true;
            var url = `TaxCategoryId=${ths.TaxCategoryData}&Type=${type}`;
            DataService.GetTaxCategoryDetails(url).then(function (response) {
                ths.isLoading = false;
                if (response.data.result.DATA.length > 0) {
                    var TaxCategoryData = response.data.result.DATA;
                    ths.FormSchema[0].Data[0].value = TaxCategoryData[0].name;
                    // ths.FormSchema[0].Data[0].disabled = true;
                    ths.FormSchema[0].Data[1].value = TaxCategoryData[0].status_id;
                    ths.FormSchema[0].Data[2].value = TaxCategoryData[0].effective_from;
                    ths.FormSchema[0].Data[3].value = TaxCategoryData[0].effective_to;
                    ths.FormSchema[0].Data[4].value = TaxCategoryData[0].rate_type;
                    // ths.FormSchema[0].Data[4].disabled = true;
                    ths.FormSchema[0].Data[5].value = TaxCategoryData[0].is_slab_rate;
                    // ths.FormSchema[0].Data[5].disabled = true;
                    ths.FormSchema[0].Data[6].value = TaxCategoryData[0].rate;
                    ths.FormSchema[2].Data[0].value = TaxCategoryData[0].description;
                    if (TaxCategoryData[0].is_slab_rate) {
                        ths.FormSchema[0].Data[6].visibility = false;
                        ths.FormSchema[1].Data[0].visibility = true;
                    } else {
                        ths.FormSchema[1].Data[0].visibility = false;
                        ths.FormSchema[0].Data[6].visibility = true;
                    }
                    if(TaxCategoryData[0].rate_type == 'F'){
                        ths.FormSchema[0].Data[6].label = ths.$t('Rate');
                        ths.isTaxRate = true;
                    }
                    else if(TaxCategoryData[0].rate_type == 'P'){
                        ths.FormSchema[0].Data[6].label = ths.$t('Rate(%)');
                        ths.isTaxRate = false;
                    }
                }
                if (response.data.result.SLABDATA.length > 0) {
                    ths.RateData = response.data.result.SLABDATA
                    ths.RateData.forEach(itm => {
                        if (itm.AmountTo == 0) {
                            itm.AmountTo = '\u221E';
                        } else {
                            itm.AmountFrom = itm.AmountFrom.toFixed(2);
                            itm.AmountTo = itm.AmountTo.toFixed(2);
                            itm.TaxRate = itm.TaxRate.toFixed(2);
                        }
                    })
                }

            })

        },
        changeRateLabel:function(x,y,z){
            var ths = this;
            var rateType= ths.FormSchema[0].Data[4].value;
            if(rateType == 'P' || rateType == 'F'){
                // Reset all TaxRate values in RateData
                ths.RateData.forEach(item => {
                        item.TaxRate = '';
                });
            }
            if(y.value == 'F'){
                ths.FormSchema[0].Data[6].label = this.$t('Rate');
                ths.FormSchema[0].Data[6].value = '';
                ths.isTaxRate = true;
            }
            else if(y.value == 'P'){
                ths.FormSchema[0].Data[6].label = this.$t('Rate(%)');
                ths.FormSchema[0].Data[6].value = '';
                ths.isTaxRate = false;
                
            }
        },
        OnSubmit(BtnType) {
            var ths = this;
            ths.isLoading = true;
            if (!ths.FormSchema[0].Data[5].value) {
                ths.RateData[0].AmountTo = '00.00';
            }
            ths.RateData.forEach(item => {
                if (item.AmountTo == "") {
                    item.AmountTo = '00.00'
                }
            })
            var obj = {
                Type: ths.Type,
                TaxCategoryId: ths.TaxCategoryId,
                TaxCategoryName: ths.FormSchema[0].Data[0].value,
                StatusId: ths.FormSchema[0].Data[1].value,
                EffectiveFrom: ths.FormSchema[0].Data[2].value,
                EffectiveTo: ths.FormSchema[0].Data[3].value,
                RateType: ths.FormSchema[0].Data[4].value,
                TaxSlab: ths.FormSchema[0].Data[5].value,
                TaxRate: ths.FormSchema[0].Data[6].value == '' ? '00.00' : (Number(ths.FormSchema?.[0]?.Data?.[6]?.value) || 0).toFixed(2),
                TaxSlabData: ths.RateData,
                TaxCategoryDescription: ths.FormSchema[2].Data[0].value
            }
            var JsonObj = JSON.stringify(obj);
            DataService.SaveTaxCategory(JsonObj).then(function (response) {
                ths.isLoading = false;
                if (response.data.result.DATA.length > 0) {
                    if (response.data.result.DATA[0].VALUE == 'UPDATE') {
                        ths.ShowAlert(ths.$t('UpdatedSuccess', [ths.$t('TaxCategory')]), "success", true, ths.$t("Alert"));
                        ths.CloseTaxCategory();
                        ths.ReloadTaxCategory();
                        ths.$parent.FetchTaxList();
                    }
                    else if (response.data.result.DATA[0].VALUE == 'EXISTS') {
                        ths.ShowAlert(ths.$t('AlreadyExists', [ths.$t('TaxCategory')]), "warning", true, ths.$t("Alert"));
                    }
                    else {
                        ths.ShowAlert(ths.$t('AddedSuccess', [ths.$t('TaxCategory')]), "success", true, ths.$t("Alert"));
                        ths.CloseTaxCategory();
                        ths.ReloadTaxCategory();
                        if (BtnType == 'SaveAndNext') {
                            ths.$emit("nextTab", true);
                        }
                    }

                } else {
                    ths.ShowAlert(ths.$t("UnknownErrorOccur"), "failure", true, ths.$t('Alert'));
                }
            })
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        Skip: function () {

            var vm = this;
            vm.$emit("nextTab", true);
        }
    }
}
</script>
