<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list ref="tgList" :listType="['List']" :IsShowAction="true" :showCheckBox="true" ModuleName="Tax"
            SubModuleCode="TaxCategory" IdentityColumn="id" :HeaderText="$t('Tax Category')" :ListData="ListingData"
            :HeaderData="Headers" :widgets="widgets" :HeaderButtons="Buttons" :TotalRecords="TotalRecords"
            :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchTaxCategoryList" :SortExp="SortExp"
            :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata"
            :SearchFields="leftSearchFields" :LegendArray="LegendArray" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:ActionButtonClick="actionButtonClick">
            <template v-slot:slotdata="{ data }">   
                <template v-if="data.column.COLUMN_NAME == 'name'">                      
                     <a class="task-dec" @click="AddTaxCategory(data.row.id)">  {{ data.row.name }}</a>                                                
                </template>
                <template v-if="data.column.COLUMN_NAME == 'effective_from'">                      
                    <span>
                        {{$options.filters.formatDate(data.row.effective_from) }}
                    </span>                                                
                </template>
                <template v-if="data.column.COLUMN_NAME == 'effective_to'">                      
                    <span>
                        {{$options.filters.formatDate(data.row.effective_to) }}
                    </span>                                               
                </template>
                 <template v-if="data.column.COLUMN_NAME == 'status_name'">                       
                    <a class="inactive-disabled">
                        <span class="text-center text-white d-block" :style="{backgroundColor: data.row.status_bg_color}"> {{ data.row.status_name }} </span>
                    </a>                                           
                </template>
                <template v-if="data.column.COLUMN_NAME == 'rate_type'">                      
                    <span v-if="data.row.rate_type=='P'">
                        {{ $t('Percentage') }}
                    </span> 
                    <span v-else>{{ $t('Flat') }}</span>                                              
                </template>
                <template v-if="data.column.COLUMN_NAME == 'is_slab_rate'">                      
                    <span v-if="data.row.is_slab_rate">                        
                        <a class="task-dec" @click="ShowSlabDeails(data.row.id,data.row.rate_type)">{{ $t('Slab') }}</a> 
                    </span> 
                    <span v-else-if ="data.row.rate_type=='P'">{{ data.row.rate }}%</span>    
                    <span v-else-if ="data.row.rate_type=='F'">{{ data.row.rate }}</span>                                            
                </template>
                <template v-if="data.column.COLUMN_NAME == 'rate'"> 
                    <span>{{ data.row.rate.toFixed(2) }}</span>   
                </template>
            </template>
        </tg-list>
        <AddTaxCategory v-if="isAddTaxCategory" @CloseTaxCategory="CloseTaxCategory" @ReloadTaxCategory="ReloadTaxCategory" :TaxCategoryData="TaxCategoryData" :ClientId="ClientId" LayoutType="Tax"></AddTaxCategory>
        <SlabDetalis v-if="IsSlabDetalis" @CloseSlabDeails="CloseSlabDeails" :TaxCategoryData="TaxCategoryData" :Type="Type" :rateType="rateType"></SlabDetalis>
    </section>
</template>
<script>
import TaxCategorySchema from './TaxCategorySchema';
import AddTaxCategory from './AddTaxCategory.vue';
import SlabDetalis from './SlabDetalis.vue';
import DataService from '../../services/DataService'
export default {
    components:{AddTaxCategory,SlabDetalis},
    data() {
        return {
            TotalRecords: 0,
            isAddTaxCategory:false,
            IsSlabDetalis:false,
            isLoading: false,
            CompanyId: null,
            Type:'Tax_Category',
            TaxCategoryData:null,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            LegendArray: [],
            SortBy: '',
            SortExp: '',
            listActions: TaxCategorySchema.listActions,
            Headers: TaxCategorySchema.TableHeader,
            IsAssignedUser: false,
            leftSearchFields: TaxCategorySchema.leftsearchSchema,
            Buttons: TaxCategorySchema.buttons,
            ListingData: [],
            searchCondition: '',
            AssigenedUser: '',
            ClientId:null,
            Headers: [
                {
                    "COLUMN_NAME": "name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "TaxName",
                    "DISPLAY_ORDER": 1,
                    settings : {
                            isInSlot: true
                        }
                },
                {
                    "COLUMN_NAME": "description",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "DESCRIPTION",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "effective_from",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "EffectiveFrom",
                    "DISPLAY_ORDER": 3,
                    settings : {
                            isInSlot: true
                        }
                },
                {
                    "COLUMN_NAME": "effective_to",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "EffectiveTo",
                    "DISPLAY_ORDER": 3,
                    settings : {
                            isInSlot: true
                        }
                },             
                {
                    "COLUMN_NAME": "rate_type",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "RateType",
                    "DISPLAY_ORDER": 3,
                    settings : {
                            isInSlot: true
                        }
                },
                {
                    "COLUMN_NAME": "is_slab_rate",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "TaxRates",
                    "DISPLAY_ORDER": 3,
                    settings : {
                            isInSlot: true
                        }                  
                },
                {
                    "COLUMN_NAME": "status_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Status",
                    "DISPLAY_ORDER": 3,
                    settings : {
                            isInSlot: true
                        }                    
                }
            ],
            UserId:null,
            CompanyId:null,
            rateType:''
        }
    },
    created:function(){
        this.UserId = this.GetUserInfo.ID;
        this.CompanyId = this.GetUserInfo.CompanyId;  
        this.FetchTaxCategoryList();
    },
    methods:{
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if (value.searchConditionJson != '') {
                    const fvalue = value.searchConditionJson.split('=')[1];
                    this.fav = fvalue;
                } else {
                    this.searchCondition = value.searchCondition;
                }
            }
            else {
                this.searchCondition = "";
            }
            this.FetchTaxCategoryList();
        },      
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {                    
                    case 'Edit':
                        action.isVisible = true;
                        break;  
                    case 'Delete':
                        action.isVisible = true;
                        break;                      
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchTaxCategoryList();
        },
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {              
                case 'EditTaxCategory':
                    this.AddTaxCategory(item.id);
                    break;
                case 'DeleteTaxCategory':
                    this.commonDeleteData(item);
                    break;                     
            }
        },
        commonHeaderButtonClick: async function(event) {
            var vm=this;
            switch (event.callbackfunction) {
                case "AddTaxCategory":
                    vm.AddTaxCategory(null);
                    break;                
                case "DeleteTaxCategory":
                    vm.commonDeleteData();
                    break;
            }
        },      
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchTaxCategoryList();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        }, 
        AddTaxCategory(val){            
            this.TaxCategoryData=val;
            this.isAddTaxCategory=true;
        }, 
        ShowSlabDeails(val,type){
            this.TaxCategoryData=val;
            this.rateType = type;
            this.IsSlabDetalis=true;
        }, 
        CloseSlabDeails(){
            this.IsSlabDetalis=false;
        },    
        CloseTaxCategory(){
            this.isAddTaxCategory=false;
        },
        ReloadTaxCategory(){
            this.FetchTaxCategoryList();
        },
        FetchTaxCategoryList(){            
            var vm = this;
            vm.isLoading=true;
            var url=`Client=&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
            DataService.GetTaxCategoryList(url).then((response)=>{             
                vm.isLoading=false;
                if(response.data.result.DATA.length>0){                    
                    vm.ListingData=response.data.result.DATA;
                    vm.TotalRecords = response.data.result.DATA[0].TOTAL_RECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                }else{
                    vm.ListingData=[];
                    vm.TotalRecords = 0;
                }
            })
        },
        commonDeleteData: function (value) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (value == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = value.id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("TaxCategory")), function () {                     
                    var url=`ids=${val}&Type=TAX_CATEGORY`;
                    DataService.RemoveMapping(url).then(function (response) {                         
                        vm.isLoading=false;
                        if(response.data.result.DATA.length>0){
                            if(response.data.result.DATA[0].VALUE=='DELETE_TAX_CATEGORY'){
                                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('TaxCategory')]), "Success", true, vm.$t('Alert'));
                                vm.FetchTaxCategoryList();
                            }else{
                                vm.ShowAlert(vm.$t('DeletedError', [vm.$t('TaxCategory')]), "failure", true, vm.$t('Alert'));
                            }                     
                        }else{
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('TaxCategory')]), "failure", true, vm.$t('Alert'));
                        }               
                    })  
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        }
    }
}
</script>
<style>
.task-dec {color: #007bff !important; text-decoration: none; background-color: transparent;}
.container-fluid.mt-2.p-0{margin-top: 0px!important;}/** discussed with Saini - Harjit did it.. 27May25***/
</style>
