<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
         <div class="container-fluid padding-t_10 px-0">
            <div class="left-menu-tab new_tab nosrcoll">
                <div class="col-md-12 p-0 ">
                    <div class="theme-primary partition">
                        <span class="p-name text-white">{{ $t('Tax') }}</span>
                    </div>
                </div>
                <div class="mt-2 mCustomScrollbar _mCS_4">
                    <a class="card mobile-tab-accordian active" href="javascript:;" data-controllername="billingrule" data-actionname="ImportFormatListing" v-on:click="showMobileTabs = !showMobileTabs;">
                        <div class="card-header" role="tab">
                            <h5><span class="mb-0"><i :class="GetSelectedModuleItem.icon"></i> {{ $t(GetSelectedModuleItem.modulename) }}<i
                                        class="fa fa-angle-down rotate-icon float-right" aria-hidden="true"></i></span>
                            </h5>
                        </div>
                    </a>
                    <div v-if="showMobileTabs">
                        <a v-for="(item,index)  in moduleList" :key="index" v-if="GetSelectedModuleItem.modulename != item.modulename" v-on:click="SetCurrentTab(item.modulename); showMobileTabs = false;" class="card mobile-tab-accordian" href="javascript:;" data-controllername="billingrule" data-actionname="ImportFormatListing">
                            <div class="card-header" role="tab">
                                <h5><span class="mb-0"><i :class="item.modulename"></i> {{ $t(item.modulename) }}
                                    <i class="fa fa-angle-down rotate-icon float-right" aria-hidden="true"></i></span>
                                </h5>
                            </div>
                        </a>
                    </div>
                    <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix">
                        <li v-for="(item,index) in moduleList" v-if="item.isVisible == true" :key="index">
                            <a checkprivilege="Yes"
                                :class="{'nav-link left-menu': true, 'active':(currentTab == item.modulename.toLowerCase()) }"
                                data-toggle="tab" role="tab" :aria-controls="item.modulename" aria-selected="false"
                                :data-text="item.modulename" :data-tabr="'#' + item.modulename"
                                v-on:click="SetCurrentTab(item.modulename)" href="javascript:;">
                                <i :class="item.icon" aria-hidden="true"></i>{{ $t(item.modulename) }}
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="right-content">
                <div class="col-md-12 p-0">
                    <div class="tab-content">
                         <TaxCategory v-if="currentTab=='tax categories'"></TaxCategory>
                         <TaxJurisdiction v-if="currentTab=='tax jurisdiction'"></TaxJurisdiction>
                         <ProductType v-if="currentTab=='product type'"></ProductType>
                    </div>
                </div>
            </div>
        </div>        
    </section>
</template>
<script>
import TaxCategory from './TaxCategory.vue';
import TaxJurisdiction from './TaxJurisdiction.vue';
import ProductType from './ProductType.vue';
export default {
    components:{TaxCategory,TaxJurisdiction,ProductType},
    data(){
        return{
            isLoading:false,
            showMobileTabs:false,
            TaxCategory:false,
            currentTab: 'Tax Categories'.toLowerCase(),
            moduleList:[              
                {
                    modulename:'Tax Categories',
                    isVisible:true,
                    icon:'fa fa-list'          
                },                
                {
                    modulename:'Tax Jurisdiction',
                    isVisible:true,
                    icon:'fa fa-bank'          
                },
                {
                    modulename:'Product Type',
                    isVisible:true,
                    icon:'fa fab fa-product-hunt'          
                }
            ]
        }
    },
     computed:{         
         GetSelectedModuleItem(){            
            var ths = this;
            var getModuleObj = ths.moduleList.filter((x) => { return x.modulename.toLowerCase() == ths.currentTab.toLowerCase()});
            return getModuleObj[0];
         }
    },
    methods:{
          SetCurrentTab: function(moduleName) { 
            this.currentTab = moduleName.toLowerCase(); 
        },
    }
}
</script>