<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Tax') }} {{ $t('Product Type Mapping') }}</h4>
                        <button type="button" class="close" v-on:click="CloseMapProductType">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" ref="AddTaxJurisdictionForm"
                            :mandatorytext='MandatoryString' v-on:OnSubmit="OnSubmit">
                                <template v-slot:tgslot-SlabRate="{ data }">                               
                                <div class="container-fluid collapse show p-0" id="collapseOne1">
                                    <div class="listing">
                                        <div class="table-responsive">
                                            <table id="Addresslist" class="table table-bordered dt-responsive nowrap mt-0">
                                                <thead class="thead-bg">
                                                    <tr>
                                                        <th width="5%" class="text-center">
                                                            #
                                                        </th>
                                                        <th class="text-center">
                                                             {{$t('AmountFrom')}}
                                                        </th>
                                                        <th class="text-center">
                                                             {{$t('AmountTo')}}
                                                        </th>
                                                        <th class="text-center">
                                                            {{$t('TaxRate')}}
                                                        </th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr v-if="RateData.length === 0">
                                                        <td class="text-center text-danger no-record" colspan="8">
                                                            {{ $t('NoRecordfound') }}
                                                        </td>
                                                    </tr>
                                                    <tr v-else v-for="(item, index) in RateData" :key="index">
                                                        <td class="text-center">{{ index+1 }}</td>
                                                        <td class="text-center">
                                                        <input type="text" class="form-control" v-model="item.AmountFrom"  :disabled="RateData.length === 1 || index === 0" @blur="validateAmounts(index)" />
                                                        </td>
                                                        <td class="text-center">
                                                            <input type="text" class="form-control" v-model="item.AmountTo" :disabled="RateData.length === 1 || index === RateData.length - 1" @blur="validateAmounts(index)" @change="updateNextAmountFrom(index)"/>
                                                        </td>
                                                        <td class="text-center">
                                                            <input type="text" class="form-control" v-model="item.TaxRate" @blur="formatTaxRate(index)"/>
                                                        </td>                                                        
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>                       
                            </template>
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../services/DataService'
export default { 
    props:{
        TaxCategoryData:{
            type:Object,
            Required:true
        }
    },   
    data() {
        return {
            MandatoryString: 'Fields marked with an asterisk (*) are mandatory.',
            isLoading: false,
            MappingId: 0,
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [ 
                        {
                            astype: "SelectField",
                            label: this.$t('Product Type'),
                            name: "ProductType",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select Product Type'),
                            validationRules: { "required": true, max: 100 },
                        },                       
                        {
                            astype: "SelectField",
                            label: this.$t('Tax Category'),
                            name: "TaxCategory",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.TaxCategoryDetails
                            },
                            placeholder: this.$t('Select Tax Category'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Rate Type'),
                            name: "RateType",
                            value: "",
                            disabled: true,
                            visibility: false, 
                            config: {
                                options: [{name:'Flat',value:'F'},{name:'Percentage',value:'P'}]                               
                            },
                            placeholder: this.$t('Select Rate Type'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('ChargeType'),
                            name: "ChargeType",
                            value: "",
                            visibility: false, 
                            disabled: true,
                            config: {
                                options: [{name:'Normal',value:'0'},{name:'Slab',value:'1'}]
                            },
                            placeholder: this.$t('Select Charge Type'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('Effective From '),
                            name: "EffectiveFrom",
                            value: new Date(),
                            visibility: false,   
                            placeholder: this.$t('Effective From'),
                            config: { format: "M/D/YYYY", minDate: new Date(),onChange: this.ChangeEffectiveFrom},
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('Effective To '),
                            name: "EffectiveTo",
                            value: '',
                            visibility: false,   
                            placeholder: this.$t('Effective To'),
                            config: { format: "M/D/YYYY",minDate: '' }
                        },
                        {
                            astype: "TextField",                                                
                            label: this.$t('Rate(%)'),
                            name: "Rate",
                            value: "",   
                            visibility: false,                         
                            placeholder: this.$t('Enter Rate'),
                            validationRules: { "required": false, max: 100 },
                        } 

                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",                            
                            name: "SlabRate",
                            value: "",   
                            visibility: false,
                            validationRules: { "required": false },
                        }
                    ]  
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.CloseMapProductType,
                },
            ],
            RateData:[]
        }
    },
    created: async function () {  
        this.GetProductType();  
        this.GetTaxCategory();    
        if(this.TaxCategoryData != null){
            this.TaxCategoryDetails(this.TaxCategoryData);
        }
    },
    methods: {
        GetProductType(){
            var vm=this;
            var url = `id=&moduleName=&type=PRODUCT_TYPE&search=&refId=`;
            vm.FormSchema[0].Data[0].config.options=[];
            DataService.GetDDLData(url).then((response)=>{
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) { 
                            response.data.DATA.forEach(item=>{
                                let obj={
                                    value:item.value,
                                    name:item.name
                                };
                                vm.FormSchema[0].Data[0].config.options.push(obj);
                            })  
                            vm.$refs.AddTaxJurisdictionForm.UpdateKeyValue();                           
                        }
                    }
                }
            })
        },
        GetTaxCategory(){
            var vm=this;
            var url = `id=&moduleName=&type=TAX_CATEGORY&search=&refId=`;
            vm.FormSchema[0].Data[0].config.options=[];
            DataService.GetDDLData(url).then((response)=>{
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) { 
                            response.data.DATA.forEach(item=>{
                                let obj={
                                    value:item.value,
                                    name:item.name
                                };
                                vm.FormSchema[0].Data[1].config.options.push(obj);
                            })  
                            vm.$refs.AddTaxJurisdictionForm.UpdateKeyValue();                           
                        }
                    }
                }
            })
        },
        TaxCategoryDetails(val){ 
            debugger
            var ths=this;
            ths.isLoading=true; 
            var Type='';
            if(typeof val=='object'){
                val=val.target.value;  
                Type="Tax_Category";
            }else{
                val=val;
                ths.MappingId= val;
                Type="Tax_Product_Type";
            }                             
            var url=`TaxCategoryId=${val}&Type=${Type}`;
            DataService.GetTaxCategoryDetails(url).then(function (response) {
                ths.isLoading=false;
                if(response.data.result.DATA.length>0){                     
                    var TaxCategoryData=response.data.result.DATA; 
                    if(Type=='Tax_Product_Type'){
                        ths.FormSchema[0].Data[0].value=TaxCategoryData[0].product_type_id;
                        ths.FormSchema[0].Data[1].value=TaxCategoryData[0].tax_id; 
                    }             
                    ths.FormSchema[0].Data[4].value=TaxCategoryData[0].effective_from;
                    ths.FormSchema[0].Data[5].value=TaxCategoryData[0].effective_to;
                    ths.FormSchema[0].Data[6].value=TaxCategoryData[0].rate;
                    ths.FormSchema[0].Data[2].value=TaxCategoryData[0].rate_type;
                    ths.FormSchema[0].Data[3].value=TaxCategoryData[0].is_slab_rate==true?1:0;   
                    ths.FormSchema[0].Data[2].visibility=true;     
                    ths.FormSchema[0].Data[3].visibility=true; 
                    ths.FormSchema[0].Data[4].visibility=true; 
                    ths.FormSchema[0].Data[5].visibility=true; 
                    if(TaxCategoryData[0].is_slab_rate){                        
                        ths.FormSchema[1].Data[0].visibility=true;
                        ths.FormSchema[0].Data[6].visibility=false;
                    }else{
                        ths.FormSchema[1].Data[0].visibility=false;  
                        ths.FormSchema[0].Data[6].visibility=true; 
                    }
                }
                if(response.data.result.SLABDATA.length>0){
                    ths.RateData=response.data.result.SLABDATA
                    ths.RateData.forEach(itm=>{
                        if(itm.AmountTo==0){
                            itm.AmountTo='\u221E';
                        }else{
                            itm.AmountFrom=itm.AmountFrom.toFixed(2);
                            itm.AmountTo=itm.AmountTo.toFixed(2);
                            itm.TaxRate=itm.TaxRate.toFixed(2);
                        }                        
                    })
                }
            })                  
        },
        formatTaxRate(index) {
            const item = this.RateData[index];
            let val = parseFloat(item.TaxRate);
            if (isNaN(val) || val < 0) {            
                this.$set(this.RateData[index], 'TaxRate', '00.00');
            } else {            
                this.$set(this.RateData[index], 'TaxRate', val.toFixed(2));
            }
        }, 
        updateNextAmountFrom(index) {        
            if (index < this.RateData.length - 1) {              
                const currentTo = parseFloat(this.RateData[index].AmountTo);
                if (!isNaN(currentTo)) {
                    const nextFrom = (currentTo + 0.01).toFixed(2);
                    this.$set(this.RateData[index], 'AmountTo', currentTo.toFixed(2));
                    this.$set(this.RateData[index + 1], 'AmountFrom', nextFrom);
                }
            }
        },
        validateAmounts(index) {            
            const item = this.RateData[index];
            const amountFrom = parseFloat(item.AmountFrom);
            const amountTo = parseFloat(item.AmountTo);
            if (isNaN(amountFrom) || isNaN(amountTo)) return;
            if (amountFrom > amountTo) {              
                this.ShowAlert(this.$t('AmountFromCannot'),"warning", true, this.$t("Alert"));                    
                if (i < this.RateData.length - 1) {              
                const currentTo = parseFloat(this.RateData[i].AmountTo);
                if (!isNaN(currentTo)) {
                    const nextFrom = (currentTo + 0.01).toFixed(2);                  
                    this.$set(this.RateData[i + 1], 'AmountFrom', nextFrom);
                }
            }
            }
            if (amountTo < amountFrom) {
                this.ShowAlert(this.$t('AmountToCannot'),"warning", true, this.$t("Alert"));  
                this.$set(this.RateData[index], 'AmountTo', '00.00');
            }
            if (index < this.RateData.length - 1) {              
                const currentTo = parseFloat(this.RateData[index].AmountTo);
                if (!isNaN(currentTo)) {
                    const nextFrom = (currentTo + 0.01).toFixed(2);
                    this.$set(this.RateData[index], 'AmountTo', currentTo.toFixed(2));
                    this.$set(this.RateData[index + 1], 'AmountFrom', nextFrom);
                }
            }
        },
        CloseMapProductType: function () {
            var vm = this;
            vm.$emit('CloseMapProductType');
        },     
        OnSubmit() {
            var ths = this;
            ths.isLoading=true;
            ths.RateData.forEach(item=>{                
                if(item.AmountTo==""){                    
                    item.AmountTo='00.00'
                }
            });
            var obj = {
                MappingId:ths.MappingId,                
                ProductTypeId:ths.FormSchema[0].Data[0].value,  
                TaxCategoryId: ths.FormSchema[0].Data[1].value,
                RateType:ths.FormSchema[0].Data[2].value, 
                TaxSlab:ths.FormSchema[0].Data[3].value,
                EffectiveFrom:ths.FormSchema[0].Data[4].value,
                EffectiveTo:ths.FormSchema[0].Data[5].value,
                TaxRate:ths.FormSchema[0].Data[6].value==''?'00.00':(Number(ths.FormSchema?.[0]?.Data?.[5]?.value) || 0).toFixed(2),  
                TaxSlabData:ths.FormSchema[0].Data[2].value==0?null:ths.RateData   
            } 
            var JsonObj=JSON.stringify(obj);
            DataService.SaveTaxCategoryProductType(JsonObj).then(function (response) { 
                ths.isLoading=false;
                if(response.data.result.DATA.length>0){ 
                    if(response.data.result.DATA[0].VALUE=='INSERT') {
                       ths.ShowAlert(ths.$t('AddedSuccess',[ths.$t('TaxProductTypeMap')]), "success", true,  ths.$t("Alert"));  
                       ths.CloseMapProductType();    
                       ths.$emit('GetProductTypeList');                
                    }
                    else if(response.data.result.DATA[0].VALUE=='UPDATE'){
                        ths.ShowAlert(ths.$t('UpdatedSuccess',[ths.$t('TaxProductTypeMap')]),"success", true, ths.$t("Alert")); 
                        ths.CloseMapProductType();   
                        ths.$emit('GetProductTypeList');                     
                    }
                    else if(response.data.result.DATA[0].VALUE=='EXISTS'){
                        ths.ShowAlert(ths.$t('AlreadyExists',[ths.$t('TaxProductTypeMap')]), "warning", true,  ths.$t("Alert"));
                    }                                
                }else{
                    ths.ShowAlert(ths.$t("UnknownErrorOccur"), "failure", true, ths.$t('Alert'));
                }
            })
        }
    }
}
</script>