<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Tax') }} {{ $t('Jurisdiction') }}</h4>
                        <button type="button" class="close" v-on:click="CloseTaxJurisdiction">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" ref="AddTaxJurisdictionForm"
                            :mandatorytext='MandatoryString' v-on:OnSubmit="OnSubmit">
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../services/DataService'
export default {
    props: {
        TaxJurisdictionData: {
            type: Object,
            Required: true
        }
    },
    data() {
        return {
            MandatoryString: 'Fields marked with an asterisk (*) are mandatory.',
            isLoading: false,
            TaxJurisdictionId: 0,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Jurisdiction Name'),
                            name: "TaxJurisdictionName",
                            value: "",
                            placeholder: this.$t('Enter Jurisdiction Name'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Jurisdiction Code'),
                            name: "JurisdictionCode",
                            value: "",
                            placeholder: this.$t('Jurisdiction Code'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "1001",
                            config: {
                                options: [{ name: 'Active', value: 1001 }, { name: 'InActive', value: 1002 }]
                            },
                            placeholder: this.$t('Select Status'),
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Country'),
                            name: "Country",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.GetState 
                            },
                            placeholder: this.$t('Select Country'),
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('State'),
                            name: "State",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select State'),
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('City'),
                            name: "City",
                            value: "",
                            placeholder: this.$t('Enter City'),
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Zip Code'),
                            name: "ZipCode",
                            value: "",
                            placeholder: this.$t('Enter Zip Code'),
                            validationRules: { "required": false, max: 100 },
                        }

                    ]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Description'),
                        name: "Description",
                        value: "",
                        config: {
                            rows: 3
                        },
                        placeholder: this.$t('Enter Tax Description'),
                        validationRules: {
                            "required": false
                        }
                    }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.CloseTaxJurisdiction,
                },
            ]
        }
    },
    created: async function () {
        this.GetCountryies();
        if (this.TaxJurisdictionData != null) {
            this.TaxJurisdictionDataDetails();
        }
    },
    methods: {
        CloseTaxJurisdiction: function () {
            var vm = this;
            vm.$emit('CloseTaxJurisdiction');
        },
        ReloadTaxJurisdiction() {
            var vm = this;
            vm.$emit('ReloadTaxJurisdiction');
        },
        GetCountryies(){
            var vm=this;
            var url = `id=&moduleName=&type=COUNTRIES&search=&refId=`;
            DataService.GetDDLData(url).then((response)=>{
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) {                             
                            response.data.DATA.forEach(item=>{
                                let obj={
                                    value:item.value,
                                    name:item.name
                                };
                                vm.FormSchema[0].Data[3].config.options.push(obj);
                            })  
                               vm.$refs.AddTaxJurisdictionForm.UpdateKeyValue();                           
                        }
                    }
                }
            })
        },
        GetState(){            
            var vm=this;
            var url = `id=&moduleName=&type=STATE&search=&refId=${vm.FormSchema[0].Data[3].value}`;
            DataService.GetDDLData(url).then((response)=>{                
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {                        
                        if (response.data.DATA.length > 0) {                             
                            response.data.DATA.forEach(item=>{
                                let obj={
                                    value:item.value,
                                    name:item.name
                                };
                                vm.FormSchema[0].Data[4].config.options.push(obj);
                            })  
                               vm.$refs.AddTaxJurisdictionForm.UpdateKeyValue();                           
                        }
                    }
                }
            })
        },
        TaxJurisdictionDataDetails() { 
            var ths = this;
            ths.TaxJurisdictionId = ths.TaxJurisdictionData.id;
            ths.FormSchema[0].Data[0].value = ths.TaxJurisdictionData.name;
            ths.FormSchema[0].Data[1].value = ths.TaxJurisdictionData.code;
            ths.FormSchema[0].Data[2].value = ths.TaxJurisdictionData.Tax_Status;
            ths.FormSchema[0].Data[3].value = ths.TaxJurisdictionData.country_id;
            ths.FormSchema[0].Data[4].value = ths.TaxJurisdictionData.state_id;
            ths.FormSchema[0].Data[5].value = ths.TaxJurisdictionData.city;
            ths.FormSchema[0].Data[6].value = ths.TaxJurisdictionData.zip_code;
            ths.FormSchema[1].Data[0].value = ths.TaxJurisdictionData.description;
            if(ths.TaxJurisdictionData.country_id != null){                
                ths.GetState();
            }
        },
        OnSubmit() {
            var ths = this;
            ths.isLoading = true;
            var obj = {
                TaxJurisdictionId: ths.TaxJurisdictionId,
                TaxJurisdictionName: ths.FormSchema[0].Data[0].value,
                TaxJurisdictionCode: ths.FormSchema[0].Data[1].value,
                TaxJurisdictionCountryId: ths.FormSchema[0].Data[3].value,
                TaxJurisdictionStateId: ths.FormSchema[0].Data[4].value,
                TaxJurisdictionCity:ths.FormSchema[0].Data[5].value,
                TaxJurisdictionZipCode:ths.FormSchema[0].Data[6].value,
                StatusId: ths.FormSchema[0].Data[2].value,
                TaxCategoryDescription: ths.FormSchema[1].Data[0].value
            }
            var JsonObj = JSON.stringify(obj);
            DataService.SaveTaxJurisdiction(JsonObj).then(function (response) {
                ths.isLoading = false;
                if (response.data.result.DATA.length > 0) {
                    if (response.data.result.DATA[0].VALUE == 'UPDATE') {
                        ths.ShowAlert(ths.$t('UpdatedSuccess', [ths.$t('TaxJurisdiction')]), "success", true, ths.$t("Alert"));
                    }
                    else if (response.data.result.DATA[0].VALUE == 'JURISDICTION_NAME_EXISTS') {
                        ths.ShowAlert(ths.$t('AlreadyExists', [ths.$t('TaxJurisdictionName')]), "warning", true, ths.$t("Alert"));
                    }
                    else if (response.data.result.DATA[0].VALUE == 'JURISDICTION_CODE_EXISTS') {
                        ths.ShowAlert(ths.$t('AlreadyExists', [ths.$t('TaxJurisdictionCode')]), "warning", true, ths.$t("Alert"));
                    }
                    else {
                        ths.ShowAlert(ths.$t('AddedSuccess', [ths.$t('TaxJurisdiction')]), "success", true, ths.$t("Alert"));
                    }
                    ths.CloseTaxJurisdiction();
                    ths.ReloadTaxJurisdiction();
                } else {
                    ths.ShowAlert(ths.$t("UnknownErrorOccur"), "failure", true, ths.$t('Alert'));
                }
            })
        }
    }
}
</script>
