<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Slab') }} {{ $t('Details') }} - {{ TaxCtaegoryName }}</h4>
                        <button type="button" class="close" v-on:click="CloseSlabDeails">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">                     
                        <div class="listing">                          
                            <div class="table-responsive">
                                        <table id="Addresslist" class="table table-bordered dt-responsive nowrap mt-0">
                                            <thead class="thead-bg">
                                                <tr>
                                                    <th width="5%" class="text-center">
                                                        #
                                                    </th>
                                                    <th class="text-center">
                                                       {{$t('AmountFrom')}}
                                                    </th>
                                                    <th class="text-center">
                                                        {{$t('AmountTo')}}
                                                    </th>
                                                    <th class="text-center">
                                                      <!-- <span>{{ $t('TaxRates') }}</span> -->
                                                      <span v-show = "rateType == 'P'">{{ $t('TaxRates') }}(%)</span>
                                                      <span v-show = "rateType == 'F'">{{ $t('TaxRates') }}</span>
                                                    </th>                                                    
                                                </tr>
                                            </thead>
                                            <tbody>                                               
                                                <tr v-if="RateData.length === 0">
                                                    <td class="text-center text-danger no-record" colspan="8">
                                                        {{ $t('NoRecordfound') }}
                                                    </td>
                                                </tr>
                                                <tr v-else v-for="(item, index) in RateData" :key="index">
                                                    <td class="text-center">{{ index+1 }}</td>
                                                    <td class="text-center">
                                                       {{item.AmountFrom.toFixed(2)}}
                                                    </td>
                                                    <td class="text-center">                                                        
                                                        {{item.AmountTo == 0?'':item.AmountTo.toFixed(2)}}                                                        
                                                    </td>
                                                    <td class="text-center">                                                       
                                                        {{item.TaxRate.toFixed(2)}}
                                                    </td>                                                    
                                                </tr>
                                            </tbody>
                                        </table>
                            </div>
                            <div class="row flex-row-reverse mt-3">
                                <div class="col-lg-6 text-right">                                    
                                    <a href="javascript:;" v-on:click="CloseSlabDeails" title="Cancel" class="btn btn-danger mr-2"><i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}</a>
                                </div> 
                            </div>                               
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../services/DataService'
export default{  
    props:{
        TaxCategoryData:{
            type:Object,
            Required:true
        },
        Type:{
            type:String,
            Required:true
        },
        rateType:{
            type:String,
            Required:true
        }
    },
    data(){
        return{         
            isLoading:false,  
            TaxCtaegoryName:'',
            RateData:[]
        }
    },
    created:async function(){  
       if(this.TaxCategoryData != null){
            this.TaxCategoryDetails();
       }
    },
    methods:{ 
        CloseSlabDeails:function(){
            var vm = this;
            vm.$emit('CloseSlabDeails');
        },
        TaxCategoryDetails(){
            var ths=this;
            ths.isLoading=true;   
            var url=`TaxCategoryId=${ths.TaxCategoryData}&Type=${ths.Type}`;
            DataService.GetTaxCategoryDetails(url).then(function (response) {
                ths.isLoading=false;                                
                if(response.data.result.SLABDATA.length>0){
                    ths.RateData=response.data.result.SLABDATA
                }
                if(response.data.result.DATA.length>0){                     
                    var TaxCategoryData=response.data.result.DATA;
                    ths.TaxCtaegoryName=TaxCategoryData[0].name;                    
                }
            })
                  
        }   
    }
}
</script>
