<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title"> {{ $t('Jurisdiction') }} {{ $t('Tax') }} {{ $t('Mapping') }}</h4>
                        <button type="button" class="close" v-on:click="CloseMapJurisdiction">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" ref="AddTaxJurisdictionForm"
                            :mandatorytext='MandatoryString' v-on:OnSubmit="OnSubmit" v-if="isAdd">
                            <template v-slot:tgslot-SlabRate="{ data }">                               
                                <div class="container-fluid collapse show p-0" id="collapseOne1">
                                    <div class="listing">
                                        <div class="table-responsive">
                                            <table id="Addresslist" class="table table-bordered dt-responsive nowrap mt-0">
                                                <thead class="thead-bg">
                                                    <tr>
                                                        <th width="5%" class="text-center">
                                                            #
                                                        </th>
                                                        <th class="text-center">
                                                              {{$t('AmountFrom')}}
                                                        </th>
                                                        <th class="text-center">
                                                             {{$t('AmountTo')}}
                                                        </th>
                                                        <th class="text-center">
                                                             {{$t('TaxRate')}}
                                                        </th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr v-if="RateData.length === 0">
                                                        <td class="text-center text-danger no-record" colspan="8">
                                                            {{ $t('NoRecordfound') }}
                                                        </td>
                                                    </tr>
                                                    <tr v-else v-for="(item, index) in RateData" :key="index">
                                                        <td class="text-center">{{ index+1 }}</td>
                                                        <td class="text-center">
                                                        <input type="text" class="form-control" v-model="item.AmountFrom"  :disabled="RateData.length === 1 || index === 0" @blur="validateAmounts(index)" />
                                                        </td>
                                                        <td class="text-center">
                                                            <input type="text" class="form-control" v-model="item.AmountTo" :disabled="RateData.length === 1 || index === RateData.length - 1" @blur="validateAmounts(index)" @change="updateNextAmountFrom(index)"/>
                                                        </td>
                                                        <td class="text-center">
                                                            <input type="text" class="form-control" v-model="item.TaxRate" @blur="formatTaxRate(index)"/>
                                                        </td>                                                        
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>                       
                            </template>
                        </dynamic-form>
                        <div class="container-fluid p-0">
                            <div class="row" v-if="IsShowList">
                                <div class="col-lg-12 my-0">
                                <div class="heading-border mt-0">
                                    <h5 class="h5"><span>{{ $t('Tax List') }}</span>
                                        <small class="float-right">
                                            <a href="javascript:void(0)" title="Add"  v-if="!isAdd" @click="ShowAssociateCategory">                    
                                                <em class="fa fa-plus pr-1"></em>{{$t('Add')}}</a>
                                            </small>
                                        </h5>
                                    </div>
                                </div>
                            </div>                           
                            <div class="listing mt-2" v-if="IsShowList">
                                        <div class="table-responsive">
                                            <table id="Addresslist" class="table table-bordered dt-responsive nowrap mt-0">
                                                <thead class="thead-bg">
                                                    <tr>
                                                        <th width="5%" class="text-center">
                                                            #
                                                        </th>
                                                        <th >
                                                            {{$t('TaxCategoryName')}}
                                                        </th>
                                                        <th >
                                                          {{$t('EffectiveFrom')}}
                                                        </th>
                                                        <th >
                                                           {{$t('EffectiveTo')}}
                                                        </th>
                                                        <th class="text-center">
                                                            {{$t('RateType')}}
                                                        </th>
                                                        <th class="text-center">
                                                            {{$t('TaxRate')}}
                                                        </th>
                                                        <th>{{ $t('Action') }}</th>
                                                    </tr>
                                                </thead>
                                                <tbody>                                           
                                                    <tr v-if="CategoryAssociateJurisdictionData.length === 0">
                                                        <td class="text-center text-danger no-record" colspan="8">
                                                            {{ $t('NoRecordfound') }}
                                                        </td>
                                                    </tr> 
                                                    <tr v-else v-for="(item, index) in CategoryAssociateJurisdictionData" :key="index">
                                                        <td class="text-center">{{ index+1 }}</td>
                                                        <td >
                                                            {{ item.tax_category_name }}
                                                        </td>
                                                        <td >
                                                            {{ $options.filters.formatDate(item.effective_from) }}
                                                        </td>
                                                        <td >
                                                           {{ $options.filters.formatDate(item.effective_to) }}
                                                        </td>
                                                        <td class="text-center">
                                                           <span v-if="item.rate_type=='P'">
                                                                {{ $t('Percentage') }}
                                                            </span> 
                                                            <span v-else>{{ $t('Flat') }}</span> 
                                                        </td>
                                                        <td class="text-center">
                                                           <span v-if="item.is_slab_rate">                        
                                                                <a class="task-dec" @click="ShowSlabDeails(item.jurisdiction_id)">{{ $t('Slab') }}</a> 
                                                            </span> 
                                                            <span v-else>{{ item.rate.toFixed(2) }}%</span>  
                                                        </td>
                                                        <td class="text-center single-action">  
                                                            <a title="Edit Mapping" class="actions-onclick" @click="EditMapping(item)"><em class="fa fa-pencil action-icon "></em></a>                                                         
                                                            <a title="Remove Mapping" class="actions-onclick" @click="RemoveMapping(item)"><em class="fa fa-times action-icon text-danger "></em></a>
                                                        </td>
                                                    </tr>                                                  
                                                </tbody>
                                            </table>
                                        </div>
                            </div>
                        </div>
                        <div class="row flex-row-reverse pt-2" v-if="IsShowList">
                            <div class="col-lg-6 text-right p-1">                                    
                                <a href="javascript:;" v-on:click="CloseMapJurisdiction" title="Cancel" class="btn btn-danger mr-2"><i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}</a>
                            </div> 
                        </div>            
                    </div>                    
                </div>
            </div>
        </div>
        <SlabDetalis v-if="IsSlabDetalis" @CloseSlabDeails="CloseSlabDeails" :TaxCategoryData="TaxCategoryData" :Type="Type"></SlabDetalis>
    </div>
</template>
<script>

import DataService from '../../services/DataService'
import SlabDetalis from './SlabDetalis.vue';
export default {
    components:{SlabDetalis},
    props: {
        TaxJurisdictionId: {
            type: Number,
            Required: true
        }
    },
    data(){
        return{
            MandatoryString: 'Fields marked with an asterisk (*) are mandatory.',
            isLoading:false,
            IsShowList:true,
            CategoryAssociateJurisdictionData:[],
            isAdd:false,
            Type:'Tax_Jurisdiction',
            TaxCategoryData:null,
            IsSlabDetalis:false,
            RateData:[{
                TaxSlabId:0,
                AmountFrom:'1.00',
                AmountTo:'\u221E',
                TaxRate:'00.00'                             
            }], 
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [                        
                        {
                            astype: "SelectField",
                            label: this.$t('Tax Category'),
                            name: "TaxCategory",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.TaxCategoryDetails
                            },
                            placeholder: this.$t('Select Tax Category'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Rate Type'),
                            name: "RateType",
                            value: "",
                            disabled: true,
                            config: {
                                options: [{name:'Flat',value:'F'},{name:'Percentage',value:'P'}]                               
                            },
                            placeholder: this.$t('Select Rate Type'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('taxSlab'),
                            name: "ChargeType",
                            value: "",
                            disabled: true,
                            config: {
                                options: [{name:'Normal',value:'0'},{name:'Slab',value:'1'}]
                            },
                            placeholder: this.$t('Select Tax Slab'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('Effective From '),
                            name: "EffectiveFrom",
                            value: new Date(),
                            visibility: true,   
                            placeholder: this.$t('Effective From'),
                            config: { format: "M/D/YYYY", minDate: new Date(),onChange: this.ChangeEffectiveFrom},
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('Effective To '),
                            name: "EffectiveTo",
                            value: '',
                            visibility: true,   
                            placeholder: this.$t('Effective To'),
                            config: { format: "M/D/YYYY",minDate: '' }
                        },
                        {
                            astype: "TextField",                                                
                            label: this.$t('Rate(%)'),
                            name: "Rate",
                            value: "",   
                            visibility: false,                         
                            placeholder: this.$t('Enter Rate'),
                            validationRules: { "required": false, max: 100 },
                        } 

                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",                            
                            name: "SlabRate",
                            value: "",   
                            visibility: false,
                            validationRules: { "required": false },
                        }
                    ]  
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.HideMapForm,
                },
            ],
            MappingId:0,
            IsEdit:false
        }
    },
    created:function(){
        var ths=this;
        ths.GetCategoryAssociateJurisdiction();        
    },
    methods:{
        ChangeEffectiveFrom(val){
            var EffectiveFrom = val;
            this.FormSchema[0].Data[4].config.minDate=EffectiveFrom;
        },
        formatTaxRate(index) {
            const item = this.RateData[index];
            let val = parseFloat(item.TaxRate);
            if (isNaN(val) || val < 0) {            
                this.$set(this.RateData[index], 'TaxRate', '00.00');
            } else {            
                this.$set(this.RateData[index], 'TaxRate', val.toFixed(2));
            }
        }, 
        updateNextAmountFrom(index) {        
            if (index < this.RateData.length - 1) {              
                const currentTo = parseFloat(this.RateData[index].AmountTo);
                if (!isNaN(currentTo)) {
                    const nextFrom = (currentTo + 0.01).toFixed(2);
                    this.$set(this.RateData[index], 'AmountTo', currentTo.toFixed(2));
                    this.$set(this.RateData[index + 1], 'AmountFrom', nextFrom);
                }
            }
        },
        validateAmounts(index) {            
            const item = this.RateData[index];
            const amountFrom = parseFloat(item.AmountFrom);
            const amountTo = parseFloat(item.AmountTo);
            if (isNaN(amountFrom) || isNaN(amountTo)) return;
            if (amountFrom > amountTo) {              
                this.ShowAlert(this.$t('AmountFromCannot'),"warning", true, this.$t("Alert"));                    
                if (i < this.RateData.length - 1) {              
                const currentTo = parseFloat(this.RateData[i].AmountTo);
                if (!isNaN(currentTo)) {
                    const nextFrom = (currentTo + 0.01).toFixed(2);                  
                    this.$set(this.RateData[i + 1], 'AmountFrom', nextFrom);
                }
            }
            }
            if (amountTo < amountFrom) {
                this.ShowAlert(this.$t('AmountToCannot'),"warning", true, this.$t("Alert"));  
                this.$set(this.RateData[index], 'AmountTo', '00.00');
            }
            if (index < this.RateData.length - 1) {              
                const currentTo = parseFloat(this.RateData[index].AmountTo);
                if (!isNaN(currentTo)) {
                    const nextFrom = (currentTo + 0.01).toFixed(2);
                    this.$set(this.RateData[index], 'AmountTo', currentTo.toFixed(2));
                    this.$set(this.RateData[index + 1], 'AmountFrom', nextFrom);
                }
            }
        },
        ShowAssociateCategory(){            
            this.GetTaxCategory();
            this.IsShowList=false;
            this.isAdd=true;
            this.IsEdit=false;
            if(!this.IsEdit){
                 this.FormSchema[0].Data[0].disabled=false;
            }
        },
        HideMapForm(){
            var ths=this;
            ths.isAdd=false;
            ths.IsShowList=true;
            ths.FormSchema[0].Data[0].value='';
            ths.FormSchema[0].Data[3].value='';
            ths.FormSchema[0].Data[4].value='';
            ths.FormSchema[0].Data[5].value='';
            ths.FormSchema[0].Data[1].value='';
            ths.FormSchema[0].Data[2].value=''; 
            ths.FormSchema[1].Data[0].visibility=false;
            ths.FormSchema[0].Data[5].visibility=false;
            ths.RateData=[];
        },
        GetTaxCategory(){
            var vm=this;
            var url = `id=&moduleName=&type=TAX_CATEGORY&search=&refId=`;
            vm.FormSchema[0].Data[0].config.options=[];
            DataService.GetDDLData(url).then((response)=>{
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) { 
                            response.data.DATA.forEach(item=>{
                                let obj={
                                    value:item.value,
                                    name:item.name
                                };
                                vm.FormSchema[0].Data[0].config.options.push(obj);
                            })  
                            vm.$refs.AddTaxJurisdictionForm.UpdateKeyValue();                           
                        }
                    }
                }
            })
        },
        EditMapping(itm){            
            var ths=this;
            ths.GetTaxCategory();
            ths.IsShowList=false;
            ths.isAdd=true;
            ths.FormSchema[0].Data[0].value=itm.tax_category_id;
            
            ths.TaxCategoryDetailss(itm.tax_category_id);
            ths.MappingId=itm.id;
            ths.IsEdit=true;
            if(ths.IsEdit){
                ths.FormSchema[0].Data[0].disabled=true;
            }
        },
        TaxCategoryDetails(val){
            var ths=this;
            ths.TaxCategoryDetailss(val.target.value);
        },
        TaxCategoryDetailss(val){            
            var ths=this;
            ths.isLoading=true;            
            var url=`TaxCategoryId=${val}&Type=Tax_Category`;
            DataService.GetTaxCategoryDetails(url).then(function (response) { 
                ths.isLoading=false;
                if(response.data.result.DATA.length>0){                     
                    var TaxCategoryData=response.data.result.DATA;                    
                    ths.FormSchema[0].Data[3].value=TaxCategoryData[0].effective_from;
                    ths.FormSchema[0].Data[4].value=TaxCategoryData[0].effective_to;
                    ths.FormSchema[0].Data[5].value=TaxCategoryData[0].rate;
                    ths.FormSchema[0].Data[1].value=TaxCategoryData[0].rate_type;
                    ths.FormSchema[0].Data[2].value=TaxCategoryData[0].is_slab_rate==true?1:0;                  
                    if(TaxCategoryData[0].is_slab_rate){                        
                        ths.FormSchema[1].Data[0].visibility=true;
                        ths.FormSchema[0].Data[5].visibility=false;
                    }else{
                        ths.FormSchema[1].Data[0].visibility=false;  
                         ths.FormSchema[0].Data[5].visibility=true;                      
                    }
                }
                if(response.data.result.SLABDATA.length>0){
                    ths.RateData=response.data.result.SLABDATA
                    ths.RateData.forEach(itm=>{
                        if(itm.AmountTo==0){
                            itm.AmountTo='\u221E';
                        }else{
                            itm.AmountFrom=itm.AmountFrom.toFixed(2);
                            itm.AmountTo=itm.AmountTo.toFixed(2);
                            itm.TaxRate=itm.TaxRate.toFixed(2);
                        }                        
                    })
                }
            })                  
        }, 
        GetCategoryAssociateJurisdiction(){            
            var ths=this;
            ths.isLoading=true;
            var url=`JurisdictionId=${ths.TaxJurisdictionId}`;
            DataService.GetCategoryAssociateJurisdiction(url).then(function (response) {                 
                ths.isLoading=false;
                if(response.data.result.DATA.length>0){  
                    ths.CategoryAssociateJurisdictionData=response.data.result.DATA;
                }
               
            })     
        },
        CloseMapJurisdiction(){
            var vm = this;
            vm.$emit('CloseMapJurisdiction');
        },  
        OnSubmit(){        
            var ths = this;
            ths.isLoading=true;             
            ths.RateData.forEach(item=>{                
                if(item.AmountTo==""){                    
                    item.AmountTo='00.00'
                }
            });
            var obj = {
                MappingId:ths.MappingId,
                TaxCategoryId: ths.FormSchema[0].Data[0].value,
                TaxJurisdictionId:ths.TaxJurisdictionId,              
                EffectiveFrom:ths.FormSchema[0].Data[3].value,
                EffectiveTo:ths.FormSchema[0].Data[4].value, 
                RateType:ths.FormSchema[0].Data[1].value, 
                TaxSlab:ths.FormSchema[0].Data[2].value,
                TaxRate:ths.FormSchema[0].Data[5].value==''?'00.00':(Number(ths.FormSchema?.[0]?.Data?.[5]?.value) || 0).toFixed(2),  
                TaxSlabData:ths.FormSchema[0].Data[2].value==0?null:ths.RateData   
            } 
            var JsonObj=JSON.stringify(obj);
            DataService.SaveTaxCategoryJurisdiction(JsonObj).then(function (response) { 
                ths.isLoading=false;
                if(response.data.result.DATA.length>0){ 
                    if(response.data.result.DATA[0].VALUE=='INSERT') {
                       ths.ShowAlert(ths.$t('AddedSuccess',[ths.$t('TaxCategoryMap')]), "success", true,  ths.$t("Alert"));
                        ths.HideMapForm();   
                    }
                    else if(response.data.result.DATA[0].VALUE=='UPDATE'){
                        ths.ShowAlert(ths.$t('UpdatedSuccess',[ths.$t('TaxCategoryMap')]),"success", true, ths.$t("Alert"));
                        ths.HideMapForm();   
                    }
                    else if(response.data.result.DATA[0].VALUE=='EXISTS'){
                        ths.ShowAlert(ths.$t('AlreadyExists',[ths.$t('TaxCategoryMap')]), "warning", true,  ths.$t("Alert"));
                    }

                    ths.GetCategoryAssociateJurisdiction();                
                }else{
                    ths.ShowAlert(ths.$t("UnknownErrorOccur"), "failure", true, ths.$t('Alert'));
                }
            })
        },
        ShowSlabDeails(val){
            this.TaxCategoryData=val;
            this.IsSlabDetalis=true;
        }, 
        CloseSlabDeails(){
            this.IsSlabDetalis=false;
        },
        RemoveMapping(val){
            var ths=this;
            ths.isLoading=true;
            var url=`ids=${val.id}&Type=Remove_Mapping`;
            DataService.RemoveMapping(url).then(function (response) { 
                ths.isLoading=false;
                if(response.data.result.DATA.length>0){
                    if(response.data.result.DATA[0].VALUE=='REMOVE_MAPPING'){
                        ths.ShowAlert(ths.$t('DeletedSuccess', [ths.$t('TaxCategoryMapping')]), "Success", true, ths.$t('Alert'));
                        ths.GetCategoryAssociateJurisdiction();
                    }else{
                        ths.ShowAlert(ths.$t('DeletedError', [ths.$t('TaxCategoryMapping')]), "failure", true, ths.$t('Alert'));
                    }                     
                }else{
                    ths.ShowAlert(ths.$t('DeletedError', [ths.$t('TaxCategoryMapping')]), "failure", true, ths.$t('Alert'));
                }               
            })  
        }   
    }
}
</script>